import dedent from 'ts-dedent';
import '../../../interface/program/nodes';
import { capitalizeFirstLetter } from '../../../utils/general';
import '../../types/core';
import { arrayToString, variableValueToGLSL } from './glsl';
export const importsToGLSL = (imports) => {
    if (!imports)
        return '';
    return arrayToString(imports, chunk => `${chunk.glsl}\n`);
};
export const constantsToGLSL = (constants) => {
    if (!constants)
        return '';
    return arrayToString(Object.entries(constants), ([name, { type, value }]) => `const ${type} ${name} = ${variableValueToGLSL({ type, value })};`);
};
export const uniformsToGLSL = (uniforms) => {
    if (!uniforms)
        return '';
    return arrayToString(Object.entries(uniforms), ([name, { type, ignore }]) => !ignore ? `uniform ${type} ${name};` : '');
};
export const attributesToGLSL = (attributes) => {
    if (!attributes)
        return '';
    return arrayToString(Object.entries(attributes), ([name, { type }]) => `varying ${type} ${name};`);
};
export const functionToGLSL = (name, glslFunction) => {
    return dedent `
    ${glslFunction.returnType} ${name}( 
      ${arrayToString(glslFunction.parameters, arg => `${arg[0]} ${arg[1]}`, ', ')}
    ) {
      ${glslFunction.body}
    }\n`;
};
export const functionsToGLSL = (functions) => {
    if (!functions)
        return '';
    return arrayToString(Object.entries(functions), ([name, glslFunction]) => functionToGLSL(name, glslFunction), '\n\n');
};
export const getNodeFunctionName = (nodeOrType) => {
    const type = typeof nodeOrType === 'object'
        ? nodeOrType.type
        : nodeOrType;
    return `get${capitalizeFirstLetter(type)}`;
};
export const getUniformName = (node, fieldName) => {
    return `u${capitalizeFirstLetter(fieldName)}_${node.id}`;
};
export const getReturnVariableName = (node) => {
    return `r${capitalizeFirstLetter(node.type)}_${node.id}`;
};
