import '../../types/core';
import '../../types/math';
export const AXES = ['x', 'y', 'z'];
export const arrayToString = (array, itemToString, separator = '\n') => {
    return array.map(itemToString).join(separator);
};
export const numToGLSL = (n) => {
    return Number.isInteger(n) ? n + '.0' : n + '';
};
export const opToGLSL = (operation, ...args) => {
    if (args.length === 0)
        return '';
    if (operation === 'pow') {
        if (args.length === 1)
            return args[0];
        let result = '';
        let end = '';
        for (let i = 0; i < args.length; i++) {
            if (i < args.length - 1) {
                result += `pow(${args[i]}, `;
                end += ')';
            }
            else {
                result += `${args[i]}`;
            }
        }
        return `(${result + end})`;
    }
    const op = (() => {
        switch (operation) {
            case 'add': return '+';
            case 'sub': return '-';
            case 'mult': return '*';
            case 'div': return '/';
            case 'avg': return '+';
        }
    })();
    let result = args.join(` ${op} `);
    if (operation === 'avg') {
        result = `(${result}) / ${numToGLSL(args.length)}`;
    }
    return result;
};
// eslint-disable-next-line @typescript-eslint/no-explicit-any
const converters = {
    'float': (value) => !value ? '0.0' : '' + numToGLSL(value),
    'int': (value) => !value ? '0' : '' + Math.floor(value),
    'vec2': (value) => !value ? 'vec2()' : `vec2(${numToGLSL(value.x)}, ${numToGLSL(value.y)})`,
    'vec3': (value) => !value ? 'vec3()' : `vec3(${numToGLSL(value.x)}, ${numToGLSL(value.y)}, ${numToGLSL(value.z)})`,
    'vec4': (value) => !value ? 'vec4()' : `vec4(${numToGLSL(value.x)}, ${numToGLSL(value.y)}, ${numToGLSL(value.z)}, ${numToGLSL(value.w)})`,
    'bool': (value) => value ? '1' : '0',
    'sampler2D': () => { throw new Error('Cannot convert texture to value'); }
};
export const variableValueToGLSL = (variable) => {
    return converters[variable.type](variable.value);
};
export const variableDefinitionToGLSL = (name, variable) => {
    return `${variable.type} ${name}${variable.value ? ` = ${variableValueToGLSL(variable)}` : ''};`;
};
