import '../program/nodes';
import { isPointInAnchor, isPointInRect } from '../utils';
export class SelectionManager {
    constructor(program) {
        this.program = program;
        // TODO use grid or quad tree to optimize selections
        // Give an "update" method that is called when nodes are moved (only when released!)
    }
    // TODO: optimize
    getChildConnections(parentNode) {
        const childConnections = [];
        this.program.nodes.forEach(node => {
            if (node === parentNode)
                return;
            Object.values(node.fields).forEach((field) => {
                if (field.value === parentNode) {
                    childConnections.push({
                        node,
                        field
                    });
                }
            });
        });
        return childConnections;
    }
    // TODO: filter anchors that currently cannot be interacted with!
    getAnchorUnderPoint(point) {
        return this.program.nodes.reduce((contender, currentNode) => {
            if (contender &&
                contender.node.layer > currentNode.layer) {
                return contender;
            }
            if (currentNode.anchor && isPointInAnchor(point, currentNode.anchor, currentNode)) {
                return {
                    anchor: currentNode.anchor,
                    node: currentNode
                };
            }
            // eslint-disable-next-line @typescript-eslint/no-unsafe-assignment
            const field = Object.values(currentNode.fields).find((currentField) => {
                return (currentField.kind === 'dynamic' &&
                    isPointInAnchor(point, currentField.anchor, currentNode));
            }, null);
            if (!field)
                return contender;
            return {
                anchor: field.anchor,
                field,
                node: currentNode,
            };
        }, undefined);
    }
    getNodeUnderPoint(point) {
        return this.program.nodes.reduce((contender, node) => {
            if (isPointInRect(point, node)) {
                if (!contender)
                    return node;
                return ((contender.layer > node.layer)
                    ? contender
                    : node);
            }
            return contender;
        }, undefined);
    }
}
