import '../../shader/types/core';
import { capitalizeFirstLetter } from '../../utils/general';
import { BORDER_WIDTH, FONT_SIZE } from '../constants';
import '../types/general';
import './constants';
export const getConnectionColor = (type, colors) => {
    const key = `nodeConnection${capitalizeFirstLetter(type)}`;
    return Object.hasOwn(colors, key) ? colors[key] : colors.fg;
};
export const renderFill = (context, rect, color) => {
    context.fillStyle = color;
    context.fillRect(rect.x, rect.y, rect.width, rect.height);
};
export const renderBorder = (context, rect, colors, zoom, active) => {
    context.lineWidth = BORDER_WIDTH / zoom;
    const color = active ? colors.nodeActiveBorder : colors.nodeBorder;
    context.strokeStyle = color;
    context.strokeRect(rect.x, rect.y, rect.width, rect.height);
};
export const renderType = (context, text, position, zoom, font) => {
    // TODO abstract font setter to util function
    context.font = `${Math.floor(FONT_SIZE / zoom)}px ${font}`;
    context.fillText(text, position.x, position.y);
};
