import '../../../interface/program/nodes';
import { isShaderNode } from '../../../interface/program/utils';
import '../../../interface/types/nodes';
import '../../../interface/types/program/program';
export const isNodePartOfCycle = (node, visited = new Set(), recStack = new Set()) => {
    if (!visited.has(node)) {
        visited.add(node);
        recStack.add(node);
        const childNodes = Object.values(node.fields)
            .filter((field) => isShaderNode(field.value))
            .map(field => field.value);
        for (let i = 0; i < childNodes.length; i++) {
            const child = childNodes[i];
            if ((!visited.has(child) && isNodePartOfCycle(child, visited, recStack)) ||
                recStack.has(child)) {
                return true;
            }
        }
    }
    recStack.delete(node);
    return false;
};
export const detectCycle = (program) => {
    const visited = new Set();
    const recStack = new Set();
    for (let i = 0; i < program.nodes.length; i++) {
        const current = program.nodes[i];
        if (isNodePartOfCycle(current, visited, recStack)) {
            return true;
        }
    }
    return false;
};
export const validateProgram = (program) => {
    return detectCycle(program);
};
export const iterateDepthFirst = (node, callback) => {
    // NOTE: assumes there are no loops...?
    Object.values(node.fields).forEach((field) => {
        if (isShaderNode(field.value)) {
            iterateDepthFirst(field.value, callback);
        }
    });
    callback(node);
};
