/* eslint-disable @typescript-eslint/no-unsafe-member-access */
import 'three';
import { getUniformName } from '../../shader/builder/utils/shader';
import { setUniform } from '../../utils/shader';
import '../types/program/program';
import { nodeCounter, nodeKeys } from './nodes';
export const isNode = (node) => {
    return (typeof node === 'object' &&
        typeof node.type === 'string' &&
        typeof node.fields === 'object');
};
export const isShaderNode = (node) => {
    return (isNode(node) &&
        Array.prototype.includes.call(nodeKeys, node.type) // NOTE: to avoid typing issues...
    );
};
export const duplicateNode = (node) => {
    const duplicate = Object.assign(Object.assign({}, node), { anchor: node.anchor ? Object.assign({}, node.anchor) : undefined, id: node.id + '_' + nodeCounter.next(), fields: Object.entries(node.fields).reduce((acc, [name, field]) => {
            acc[name] = Object.assign(Object.assign({}, field), { anchor: Object.assign({}, field.anchor) });
            return acc;
        }, {}) });
    return duplicate;
};
export const setAllUniforms = (program, shaderMaterial) => {
    program.nodes.forEach(node => {
        Object.keys(node.fields).forEach(fieldName => {
            const field = node.fields[fieldName];
            if (!isNode(field.value) && !field.internal) {
                const uniformName = getUniformName(node, fieldName);
                setUniform(uniformName, field.value, shaderMaterial);
            }
        });
    });
};
