export const capitalizeFirstLetter = (value) => {
    return value.charAt(0).toUpperCase() + value.slice(1);
};
// based on https://stackoverflow.com/a/7225450
export const camelCaseToTitleCase = (text, toLowerCase = false) => {
    const result = text.replace(/([A-Z])/g, ' $1');
    const titleCase = (result.charAt(0).toUpperCase() + result.slice(1));
    if (toLowerCase)
        return titleCase.toLocaleLowerCase();
    return titleCase;
};
export const pushIfNotIncluded = (arr, entry) => {
    if (!arr.includes(entry))
        arr.push(entry);
};
// Kudos https://stackoverflow.com/a/64489535
export const groupBy = (array, predicate) => array.reduce((acc, value) => {
    var _a;
    (acc[_a = predicate(value)] || (acc[_a] = [])).push(value);
    return acc;
}, {});
export const promptDownload = (dataURL, name) => {
    const link = document.createElement('a');
    link.href = dataURL;
    link.download = name;
    link.click();
};
