import { nodeConfigs } from '../../shader/builder/nodes';
import '../../shader/types/programBuilder';
import '../../types/utils';
import { camelCaseToTitleCase } from '../../utils/general';
import { ANCHOR_SIZE, EDGE_PADDING, FONT_SIZE, NODE_EXTRA_SPACES, NODE_WIDTH, SPACING } from '../constants';
import { getNodeHeight } from '../utils';
// TODO: find solution without side effects  
export const nodeCounter = {
    count: 0,
    set(value) {
        this.count = value;
    },
    next() {
        return this.count++;
    },
    current() {
        return this.count;
    }
};
// Util object 
export const nodeKeys = Object.values(nodeConfigs).map(config => config.name);
export const createNode = (nodeConfig, startX = 0, startY = 0) => {
    var _a;
    const type = nodeConfig.name;
    const fieldsData = nodeConfig.fields;
    const returnType = nodeConfig.returnType;
    const layer = nodeCounter.next(); // TODO fix side effect
    let maxTextCharCount = camelCaseToTitleCase((_a = nodeConfig.displayName) !== null && _a !== void 0 ? _a : nodeConfig.name).length
        + 2; // Add two to account for wide title
    let maxGlslTypeCharCount = nodeConfig.returnType.length;
    Object.keys(fieldsData).forEach(name => {
        const field = fieldsData[name];
        if (field.internal || field.hidden)
            return;
        maxTextCharCount = Math.max(camelCaseToTitleCase(name).length, maxTextCharCount);
        maxGlslTypeCharCount = Math.max(fieldsData[name].type.length, maxGlslTypeCharCount);
    });
    const fieldChars = (maxTextCharCount + maxGlslTypeCharCount + 2.0);
    const width = Math.max(fieldChars * (0.6 * FONT_SIZE) + 1.2 * EDGE_PADDING, NODE_WIDTH);
    const numberOfVisibleFields = Object.values(fieldsData)
        .filter(fieldData => (!fieldData.internal && !fieldData.hidden))
        .length;
    const height = getNodeHeight(numberOfVisibleFields);
    let i = NODE_EXTRA_SPACES;
    const fields = Object.entries(fieldsData)
        .reduce((acc, [name, fieldInit]) => {
        const minYOffset = (i * height / (numberOfVisibleFields + NODE_EXTRA_SPACES));
        let y = 0.0;
        if (!fieldInit.internal && !fieldInit.hidden) {
            y =
                1.25 * EDGE_PADDING +
                    Math.max((SPACING + FONT_SIZE) * i, minYOffset);
            i++;
        }
        const field = Object.assign(Object.assign({}, fieldInit), { anchor: {
                size: ANCHOR_SIZE,
                x: 0.0,
                y: y - ANCHOR_SIZE / 4.0,
                type: 'field'
            } }); // TODO why is this necessary?
        acc[name] = field;
        return acc;
    }, {});
    const anchor = type !== 'root'
        ? {
            size: ANCHOR_SIZE,
            x: width,
            y: height / 2.0,
            type: 'node'
        }
        : undefined;
    return {
        type,
        name: nodeConfig.displayName,
        x: startX,
        y: startY,
        width,
        height,
        anchor,
        layer,
        fields,
        returnType,
        id: '' + nodeCounter.current()
    };
};
const buildNodeCreator = (nodeConfig) => (startX = 0, startY = 0) => {
    return createNode(nodeConfig, startX, startY);
};
const shaderNodeCreators = Object.values(nodeConfigs).map(config => buildNodeCreator(config));
export const nodeCreatorMap = shaderNodeCreators.reduce((acc, nodeFunction) => {
    acc[nodeFunction().type] = nodeFunction;
    return acc;
}, {});
